

.icon {
  object-fit: contain;
  width: 100px;
}

.Newsitems > div:last-child .Newsitem {
  margin-bottom: 0;
}
.newsitems {

  div.items {
    box-shadow: -0.2em 0.3em 0.4em #bfb4b4;
    flex-basis: 100%;
    padding: 1em;
    margin: 2%;
    max-width: 96%;
    background-size: 100%;
    background-position: bottom;
    background-repeat: no-repeat;

    img.image {
      max-width: 100%;
      border-radius: 0.7em;
    }



    .title-heading {
      padding: 1em;
      vertical-align: super;
      font-size: 1.2em;
      cursor: pointer;
    }

    .routable{
      cursor: pointer;
    }

    .description {
      font-size: 1em;
      font-weight: unset;
    }
  }

  .image-section {
    position: relative;
  }

  .play-icon {
    color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: 3.12em;
    transform: translate(-50%, -50%);
  }

  .premium-0 {
    background: white;
  }

  .premium-1 {
    background-image: linear-gradient(rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.7)),
    url("/assets/newsfeed/sunglass-background.png");
  }

  .premium-2 {
    background-image: linear-gradient(rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.7)),
    url("/assets/newsfeed/lock-background.png");
  }

}


.image-item {

  div.number {
    top: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    background: #57535380;
    font-size: 1.5em;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  img {
    position: absolute;
  }
}

